import React, { Fragment } from 'react';
import Img from 'gatsby-image';
import { useStaticQuery, graphql } from 'gatsby';

import SEO from '../components/SEO';
import CTA from '../components/CTA';

const AboutPage = () => {
  const query = useStaticQuery(graphql`
    query {
      wave: file(relativePath: { eq: "wave.png" }) {
        childImageSharp {
          fluid(maxWidth: 100, quality: 100) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `);

  return (
    <Fragment>
      <SEO
        title="About"
        description="Hi there! I'm Safdar. I'm a freelance software developer based in Karachi, Pakistan, who enjoys building things for the web."
      />
      <div className="contain-sm my-8 md:my-16">
        <h1 className="flex items-center text-3xl md:text-4xl md:text-5xl">
          Hi there!
          <span className="w-12 mx-3">
            <Img fluid={query.wave.childImageSharp.fluid} />
          </span>
          I&apos;m Safdar.
        </h1>
        <p className="text-mdgray my-8 md:text-lg">
          I'm a freelance software developer based in Karachi, Pakistan, who
          enjoys building things for the web.
        </p>
        <p className="text-mdgray my-8 md:text-lg">
          Currently, I'm working with companies and agencies around the world,
          building Web sites and applications with a strong focus on user
          experience (UX) design, accessibility, and performance.
        </p>
        <h2 className="text-xl md:text-3xl mb-4">Tools &amp; Technologies</h2>
        <p className="text-mdgray my-8 md:text-lg">
          Here are a few tools &amp; technologies I've worked within the past.
          I'm always open to adding more to this list if they will help the
          project in need.
        </p>
        <div className="max-w-md">
          <ul className="ml-5 flex flex-wrap md:text-lg text-mdgray list-disc">
            <li className="w-3/6 p-2">HTML</li>
            <li className="w-3/6 p-2">CSS</li>
            <li className="w-3/6 p-2">JavaScript</li>
            <li className="w-3/6 p-2">React</li>
            <li className="w-3/6 p-2">Redux</li>
            <li className="w-3/6 p-2">Gatsby</li>
            <li className="w-3/6 p-2">GraphQL</li>
            <li className="w-3/6 p-2">Next.js</li>
            <li className="w-3/6 p-2">Node.js</li>
            <li className="w-3/6 p-2">Express</li>
            <li className="w-3/6 p-2">MongoDB</li>
          </ul>
        </div>
      </div>
      <section className="contain-sm mt-32">
        <CTA
          title="Ready to get started?"
          description="You know about me, letâ€™s talk about you."
          buttonText="Get In Touch"
        />
      </section>
    </Fragment>
  );
};

export default AboutPage;
